@import url(https://fonts.googleapis.com/css2?family=Newsreader&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: #212121;
  height: 100vh;
}
html {
  font-size: 28px;
}

@media only screen and (max-width: 850px) {
  html {
    font-size: 18px;
  }
}
@media only screen and (max-width: 700px) {
  html {
    font-size: 16px;
  }
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 14px;
  }
}

.App {
  color: black;
  font-family: "Newsreader", serif;
}

.main-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.recipe {
  background: whitesmoke;
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 20rem;
  margin: 0.7rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.image {
  width: 10rem;
  height: 13rem;
  margin: 0.5rem 0;
  right: auto;
  border-radius: 5px;
  transition: 0.61s;
}

.image:hover {
  cursor: pointer;
  transform: scale(1.051, 1.051);
}

.recipe-title {
  font-size: 0.9rem;
  font-weight: 700;
  letter-spacing: 3px;
  margin: 0.5rem 0;
  text-align: center;
  color: black;
  text-decoration: none;
  transition: 0.2s;
}

.recipe-title:hover {
  color: rgba(0, 0, 0, 0.8);
}

.recipe-desc {
  font-size: 0.6rem;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 700;
  margin: 0.5rem 0;
  letter-spacing: 0.1rem;
}

.search-form {
  display: flex;
  margin: 2rem;
  justify-content: center;
}

.search-bar {
  margin: 1rem;
  border-radius: 17px;
  border: none;
  padding: 0.5rem;
  transition: 0.7s;
}

.search-bar:focus {
  border-radius: 0.5px;
  outline: none;
  border: none;
}

.search-btn {
  margin: 1rem;
  padding: 0.3rem 1rem;
  border: none;
  border-radius: 17px;
  transition: 0.5s;
}

.search-btn:hover {
  cursor: pointer;
  background: rgba(245, 245, 245, 0.8);
  color: rgba(0, 0, 0, 0.8);
}

.search-btn:focus {
  border-radius: 0.5rem;
  outline: none;
}

.heading {
  margin: 1rem;
  color: white;
  font-size: 2.2rem;
  text-align: center;
  font-weight: 400;
  letter-spacing: 5px;
}
